@mixin fontFamily($family) {
  $main: 'DM Sans', sans-serif;
  $secondary: 'Titan One';
  @if $family == 'main' {
    font-family: $main;
  }
  @if $family == 'secondary' {
    font-family: $secondary;
  }
}

@mixin transition($properties...) {
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  @if length($properties) >0 {
    transition-property: $properties;
  }
}

@mixin font($fontsize, $fontweight, $lineheight, $letterspacing: normal) {
  font-size: $fontsize;
  font-weight: $fontweight;
  line-height: $lineheight;
  letter-spacing: $letterspacing;
}

@mixin mq($type) {
  $mobile: 480px;
  $tablet: 768px;
  $desktop: 1280px;
  @if $type == 'mobile' {
    @media screen and (min-width: $mobile) {
      @content;
    }
  }
  @if $type == 'mobile-only' {
    @media screen and (max-width: #{$tablet - 0.02}) {
      @content;
    }
  }
  @if $type == 'tablet' {
    @media screen and (min-width: $tablet) {
      @content;
    }
  }
  @if $type == 'tablet-only' {
    @media screen and (min-width: $tablet) and (max-width: #{$desktop - 0.02}) {
      @content;
    }
  }
  @if $type == 'desktop' {
    @media screen and (min-width: $desktop) {
      @content;
    }
  }
  @if $type == 'not-desktop' {
    @media screen and (max-width: #{$desktop - 0.02}) {
      @content;
    }
  }
  @if $type == 'mobile-tablet' {
    @media screen and (min-width: $mobile) and (max-width: #{$tablet - 0.02}) {
      @content;
    }
  }
  @if $type == 'retina' {
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      @content;
    }
  }
}

@mixin productImg ($background-image1x, $background-image2x) {
  background-image: $background-image1x;
  // position: absolute;
  background-size: contain;
  background-repeat: no-repeat;

    @include mq (retina) {
      background-image: $background-image2x;

      }
}

@mixin productCard ($background-color) {
  background-color: $background-color;
  border-radius: 24px;
  // transform-style: preserve-3d;
  // position: absolute;
}